@media screen and (max-width:1024px) {
    .min {
        display: block;
    }
    .burger {
        display: block;
    }
    .btn_mnu {
        opacity: 1;
        position: absolute;
        top: 10px;
        right: 5px;
    }
    .header {
        margin-top: 10px;
        &_wrap {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
        &_logo {
            position: static;
        }
        &_menu {
            &.active {
                display: block;
            }
            display: none;
            position: absolute;
            top: 0; left: 0;
            z-index: 109;
            width: 100%;
            height: 100vh;
            background-color: #eee;
        }
        &_ul {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            text-align: center;
            margin-top: 20vh;
        }
        &_li {
            margin: 5px 0;
            padding: 10px 0;
            border-bottom: 1px solid @blue;
            &:last-child {
                border-bottom: none;
            }
        }
        &_a {
            font-family: 'RobotoRegular','Arial',sans-serif;
            letter-spacing: 1px;
        }
        &_lang {
            &.active {
                display: block;
            }
            display: none;
            z-index: 110;
            position: absolute;
            bottom: 0; left: 0;
            width: 100%;
            &--item {
                background-color: #d9d9d9;
                width: 100%;
                border: none;
                display: flex;
                justify-content: center;
            }
            &--eng {
                bottom: 45px;
            }
            svg {
                display: none;
            }
        }
    }
    .intro {
        background-size: cover;
        border-bottom: 15px solid @blue;
        text-align: center;
        &::after {
            display: none;
        }
        &_callback {
            float: inherit;
            position: static;
            margin: 0 auto;
            display: inline-block;
            width: 75%;
        }
        &_clear {
            display: none;
        }
        &_tel {
            font-size: 30px;
        }
        &_text {
            float: inherit;
            margin: 50px auto 0;
            position: static;
            &--h1 {
                font-size: 48px;
            }
            &--bottom {
                justify-content: center;
            }
            &--p {
                text-align: left;
            }
        }
    }
    .service {
        &_wrap {
            justify-content: center;
        }
        &_h2 {
            text-align: center;
        }
        &_item {
            &:nth-child(1){
                margin-left: 22px;
            }
            &:nth-child(3) {
                margin-right: 22px;
            }
            &:nth-child(4){
                margin-left: 22px;
            }
            &:nth-child(5){
                margin-right: 22px;
            }
        }
    }
    .slider {
        padding: 65px 0;
    }
    .know {
        &_content {
            width: 100%;
            height: auto;
            min-height: 360px;
        }
    }
    .contacts {
        margin-left: 0;
    }
    .intro-contacts .intro_text, 
    .intro.intro-sertificates .intro_text {
        width: 80%;
    }
}
@media screen and (max-width:600px) {
    .social {
        display: none;
    }
    .header {
        height: 70px;
        margin: 0;
        order: 1;
        &_wrap {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
        &_logo {
            position: absolute;
            top: 70px;
            left: 50%;
            transform: translate(-50%);
        }

    }
    .intro {
        order: 2;
        &_callback {
            position: absolute;
            bottom: 0; top: auto;
            left: 50%;
            transform: translate(-50%);
        }
        &_tel {
            font-size: 22px;
        }
        &_text {
            display: none;
        }
    }
    .slider {
        order: 3;
        margin-top: 0;
        padding: 35px 0;
        border-bottom: none;
        &_content {
            text-align: center;
           padding: 0
        }
        &_h3 {
            font-size: 22px;
        }
        &_p {
            font-size: 16px;
            margin: 20px auto 25px;
        }
    }
    .service {
        margin-top: 0;
        order: 4;
        &_h2 {
            font-size: 40px;
        }
        &_h3 {
            font-size: 22px;
            text-align: center;
        }
        &_item {
            padding: 35px 20px 0;
            justify-content: flex-start;
            height: 320px;
            br {
                display: none;
            }
        }
        &_p {
            margin-top: 20px;
        }
        &_btn {
            text-align: center;
            margin-top: 35px;
        }
        
        
    }
    .know {
        margin-top: 50px;
        order: 5;
        &_content {
            min-height: inherit;
            padding-bottom: 35px;
        }
        &_h3 {
            margin: 50px auto 20px;
            font-size: 22px;
        }
        &_p {
            text-align: center;
            margin: 0;
            padding: 0 10px;
        }
        &_line {
            display: none;
        }
        &_a {
            bottom: -40px;
            font-size: 14px;
            height: 40px;
            width: 220px;
            &--plus {
                width: 50px;
                line-height: 40px;
            }
            &--text {
                width: 170px;
                line-height: 40px;
            }
        }
    }
    .footer {
        order: 6;
        padding-top: 40px;
        &_h3 {
            font-size: 22px;
        }
        &_form {
            text-align: center;
            margin: 50px auto 0;
            &--input {
                font-size: 14px;
                width: 40%;
                padding: 10px 0;
                &:nth-child(2) {
                    margin-left: 4%;
                }
                &:nth-child(3) {
                    width: 84%;
                    margin: 25px auto 0;
                }
            }
        }
        &_bottom {
            justify-content: center;
            align-items: center;
            flex-direction: column;
            &--item {
                margin: 10px 0;
                &:nth-child(2) {
                    display: flex;
                    div {
                        margin-left: 10px;
                    }
                }
                &:nth-child(3) {
                    p {
                        display: inline;
                    }
                }
            }
        }
        &_arrow {
            cursor: pointer;
            margin-top: 50px;
            width: 26px;
            fill: #fff;
            transform: rotate(180deg);
        }
    }
    .contacts {
        margin: 110px 0 0;
        order: 3;
    }
    .intro-contacts .intro_text, 
    .intro.intro-sertificates .intro_text {
        width: 100%;
        font-size: 22px;
        height: 115px;
        bottom: -115px;
    }
    .intro-contacts .intro_text--item, 
    .intro.intro-sertificates .intro_text--item {
        height: 115px;
        border-top: 15px solid @blue;
    }
    .intro-contacts .intro_text--h1, 
    .intro.intro-sertificates .intro_text--h1 {
        font-size: 28px;
    }
}
