@font-face {
  font-family: 'RobotoRegular';
  src: url('../fonts/RobotoRegular.eot');
  src: url('../fonts/RobotoRegular.eot') format('embedded-opentype'), url('../fonts/RobotoRegular.woff2') format('woff2'), url('../fonts/RobotoRegular.woff') format('woff'), url('../fonts/RobotoRegular.ttf') format('truetype'), url('../fonts/RobotoRegular.svg#RobotoRegular') format('svg');
}

@font-face {
  font-family: 'RobotoBold';
  src: url('../fonts/RobotoBold.eot');
  src: url('../fonts/RobotoBold.eot') format('embedded-opentype'), url('../fonts/RobotoBold.woff2') format('woff2'), url('../fonts/RobotoBold.woff') format('woff'), url('../fonts/RobotoBold.ttf') format('truetype'), url('../fonts/RobotoBold.svg#RobotoBold') format('svg');
}

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
  color: inherit;
}

a:hover {
  text-decoration: none;
}

body {
  font-family: 'RobotoRegular', 'Arial', sans-serif;
}

ul,
li {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

:active,
:hover,
:focus {
  outline: 0;
  outline-offset: 0;
}

input {
  border: none;
}

button {
  background: none;
  border: none;
}

* {
  box-sizing: border-box;
}

.container {
  max-width: 1130px;
  padding: 0 15px;
  width: 100%;
  margin: 0 auto;
}

.header {
  margin-top: 10px;
}

.header_wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header_logo {
  position: relative;
  top: 35px;
  z-index: 11;
}

.header_ul {
  display: flex;
}

.header_li {
  margin: 0 25px;
}

.header_a {
  font-family: 'RobotoBold', 'Arial', sans-serif;
  color: #002856;
  transition: .3s all;
}

.header_a.active {
  color: #d41317;
}

.header_a:hover {
  color: #d41317;
}

.header_lang {
  position: relative;
}

.header_lang--item {
  display: flex;
  align-items: center;
  background-color: #eee;
  cursor: pointer;
  height: 45px;
  width: 121px;
  border: 1px solid #e8e8e8;
  transition: .3s all;
  z-index: 10;
}

.header_lang--item:hover {
  background-color: #fff;
}

.header_lang--left {
  display: flex;
  margin-left: 15px;
}

.header_lang--eng {
  display: none;
  position: absolute;
  bottom: -45px;
}

.header_lang--eng.active {
  display: flex;
}

.header_lang svg {
  min-width: 12px;
  width: 12px;
  height: auto;
  margin-left: 20px;
  transition: .3s all;
  position: relative;
  top: -2px;
}

.header_lang svg.active {
  transform: rotate(180deg);
}

.header_lang img {
  height: 20px;
}

.header_lang p {
  margin-left: 5px;
  font-size: 14px;
  color: #323232;
}

.min {
  display: none;
}

body.active {
  overflow: hidden;
}

.burger {
  display: none;
}

.btn_mnu {
  width: 50px;
  height: 50px;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  cursor: pointer;
  position: relative;
  z-index: 110;
  opacity: 0;
  transition: .3s all;
}

.btn_row {
  height: 2px;
  width: 60%;
  margin: 2px 0;
  background-color: #002856;
  position: relative;
  transition: all .5s;
}

.burger.active .btn_row:first-child {
  transform: rotate(-135deg);
  top: 6px;
}

.burger.active .btn_row:last-child {
  transform: rotate(-225deg);
  top: -6px;
}

.burger.active .btn_row:nth-child(2) {
  opacity: 0;
}

.footer {
  margin-top: 120px;
  min-height: 572px;
  background-color: #002856;
  padding-top: 65px;
  color: #fff;
}

.footer_h3 {
  font-size: 35px;
  text-align: center;
}

.footer_form {
  margin: 70px auto 0;
  max-width: 865px;
}

.footer_form--input {
  border-bottom: 1px solid #fff;
  background-color: transparent;
  padding: 7px;
  width: 47.5%;
  color: #fff;
}

.footer_form--input::placeholder {
  color: #fff;
}

.footer_form--input:nth-child(2) {
  margin-left: 4%;
}

.footer_form--input:nth-child(3) {
  margin-top: 15px;
  display: block;
  border: none;
  border-bottom: 1px solid #fff;
  width: 100%;
}

.footer_form--submit {
  cursor: pointer;
  display: block;
  background-color: transparent;
  color: #fff;
  text-align: center;
  margin: 20px auto 0;
}

.footer_bottom {
  position: relative;
  margin: 50px auto 0;
  max-width: 865px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.footer_bottom--item {
  padding: 0 5px;
}

.footer_bottom--item:nth-child(2) {
  display: flex;
}

.footer_bottom--item:nth-child(2) div {
  margin-left: 10px;
}

.footer_bottom--item:nth-child(3) p {
  display: inline;
}

.footer_arrow {
  position: absolute;
  right: 0;
  cursor: pointer;
  margin-top: 50px;
  width: 26px;
  fill: #fff;
  transform: rotate(180deg);
}

.social {
  position: fixed;
  top: 50%;
  right: 50px;
  transform: translate(0, -50%);
  display: flex;
  flex-direction: column;
  z-index: 101;
}

.social_fb {
  margin-top: 20px;
}

.main {
  display: flex;
  flex-direction: column;
}

.intro {
  background: url(../img/main.jpg) no-repeat;
  min-height: 534px;
  position: relative;
}

.intro::after {
  content: '';
  position: absolute;
  bottom: 212px;
  left: 100px;
  height: 15px;
  width: 500px;
  background-color: #002856;
}

.intro_callback {
  float: right;
  margin-right: 60px;
  position: relative;
  top: -20px;
}

.intro_callback--item {
  font-family: 'RobotoBold', 'Arial', sans-serif;
  display: flex;
  flex-direction: column;
  text-align: center;
  color: #002856;
  background-color: #eee;
  padding: 10px 15px;
}

.intro_clear {
  clear: both;
}

.intro_tel {
  font-size: 35px;
}

.intro_time {
  font-size: 14px;
}

.intro_text {
  float: right;
  margin: 240px 60px 0 0;
  position: relative;
  top: -20px;
  height: 400px;
  background-color: #eee;
  z-index: 10;
}

.intro_text--item {
  padding: 30px 50px 0px;
}

.intro_text--h1 {
  color: #002856;
  font-size: 58px;
  line-height: 1;
}

.intro_text--bottom {
  margin-top: 65px;
  display: flex;
  align-items: flex-start;
}

.intro_text--p {
  max-width: 500px;
  line-height: 1.5;
  letter-spacing: 1px;
  margin-left: 30px;
}

.intro_text--line {
  margin-top: 5px;
  width: 115px;
  height: 3px;
  background-color: #002856;
}

.intro_text--a {
  color: #fff;
  background-color: #002856;
  display: flex;
  justify-content: space-around;
  width: 234px;
  height: 52px;
  position: absolute;
  bottom: -52px;
  right: 45px;
}

.intro_text--a__plus {
  display: block;
  text-align: center;
  width: 52px;
  font-size: 20px;
  line-height: 52px;
  transition: .3s all;
}

.intro_text--a__plus:hover {
  background-color: #d41317;
}

.intro_text--a__text {
  display: block;
  text-align: center;
  width: 182px;
  line-height: 52px;
  transition: .3s all;
  background-color: #d41317;
}

.intro_text--a__text:hover {
  background-color: #002856;
}

.service {
  margin-top: 120px;
  padding-top: 50px;
  position: relative;
}

.service::after {
  content: '';
  position: absolute;
  height: 326px;
  width: 100%;
  top: 0;
  left: 0;
  background-color: #002856;
  z-index: -1;
}

.service_content {
  max-width: 960px;
  margin: 0 auto;
  width: 100%;
  padding: 0 15px;
}

.service_h2 {
  color: #fff;
  font-size: 55px;
}

.service_wrap {
  margin-top: 35px;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
}

.service_item {
  width: 279px;
  height: 364px;
  background-color: #eee;
  padding: 35px 0px 20px 25px;
  position: relative;
  margin: 22px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.service_item:nth-child(1) {
  margin-left: 0;
}

.service_item:nth-child(3) {
  margin-right: 0;
}

.service_item:nth-child(4) {
  margin-left: 0;
}

.service_item:nth-child(5) {
  margin-right: 0;
}

.service_item::after {
  content: '';
  border: 3px solid #eee;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 12px;
  left: 7px;
}

.service_h3 {
  font-size: 28px;
  color: #002856;
}

.service_p {
  margin-top: 10px;
  max-width: 220px;
  line-height: 1.5;
  letter-spacing: 0.5px;
}

.service_btn {
  display: inline-block;
  transition: .3s all;
  color: #d41317;
}

.service_btn:hover {
  color: #002856;
}

.slider {
  margin-top: 65px;
  padding: 65px 0 130px;
  background-color: #eee;
  color: #002856;
  border-bottom: 15px solid #002856;
}

.slider_content {
  text-align: center;
  padding: 0 50px;
}

.slider_h3 {
  font-size: 35px;
}

.slider_p {
  max-width: 750px;
  display: inline-block;
  margin: 45px auto 50px;
  font-size: 31px;
}

.slider_line {
  width: 113px;
  height: 3px;
  background-color: #002856;
  margin: 0 auto;
}

.slick-next {
  right: 0;
}

.slick-prev {
  left: 0;
}

.slick-prev:before,
.slick-next:before {
  font-size: 55px;
  color: #002856;
}

.slick-dots li.slick-active {
  background-color: transparent;
  border: 2px solid #002856;
  width: 9px;
  height: 9px;
  border-radius: 100%;
}

.slick-dots li {
  background-color: #002856;
  border: 2px solid #002856;
  width: 7px;
  height: 7px;
  border-radius: 100%;
}

.slick-dots li button:before {
  content: '';
}

.slick-dots {
  position: absolute;
  bottom: -120px;
  left: 50%;
  transform: translate(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
}

.know {
  margin-top: 125px;
}

.know_content {
  width: 920px;
  height: 360px;
  background-color: #eee;
  position: relative;
  margin: 0 auto;
  text-align: center;
}

.know_h3 {
  margin: 55px auto 50px;
  display: inline-block;
  font-size: 35px;
  color: #002856;
}

.know_bottom {
  display: flex;
  align-items: flex-start;
  justify-content: center;
}

.know_p {
  max-width: 500px;
  line-height: 1.5;
  letter-spacing: 0.5px;
  margin-left: 30px;
  text-align: left;
}

.know_line {
  margin-top: 5px;
  width: 115px;
  height: 3px;
  background-color: #002856;
}

.know_a {
  color: #fff;
  background-color: #002856;
  display: flex;
  justify-content: space-around;
  width: 262px;
  height: 52px;
  position: absolute;
  bottom: -52px;
  right: 0px;
}

.know_a--plus {
  display: block;
  text-align: center;
  width: 52px;
  font-size: 20px;
  line-height: 52px;
  transition: .3s all;
}

.know_a--plus:hover {
  background-color: #d41317;
}

.know_a--text {
  display: block;
  text-align: center;
  width: 210px;
  line-height: 52px;
  transition: .3s all;
  background-color: #d41317;
}

.know_a--text:hover {
  background-color: #002856;
}

.intro.intro-contacts {
  background: url(../img/contact.jpg) no-repeat;
  height: 360px;
  min-height: inherit;
  position: relative;
  background-attachment: fixed;
}

.intro.intro-contacts::after {
  display: none;
}

.intro.intro-sertificates {
  background: url(../img/certificat.jpg) no-repeat;
  min-height: 534px;
  position: relative;
}

.intro.intro-sertificates::after {
  bottom: -15px;
  left: 0;
}

.intro-contacts .intro_text,
.intro.intro-sertificates .intro_text {
  float: inherit;
  text-align: center;
  margin: 0 auto;
  height: 250px;
  width: 60%;
  display: inline-block;
  position: absolute;
  bottom: -125px;
  top: auto;
  left: 50%;
  transform: translate(-50%);
}

.intro-contacts .intro_text--item,
.intro.intro-sertificates .intro_text--item {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  height: 250px;
  padding: 0px 40px;
}

.intro-contacts .intro_clear,
.intro.intro-sertificates .intro_clear {
  display: none;
}

.contacts {
  margin: 160px 0 0 250px;
}

.contacts-item {
  margin: 20px 0;
}

.contacts-item--h3 {
  font-size: 28px;
  color: #002856;
}

.contacts-item--p {
  margin-top: 15px;
}

.overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.8);
  display: none;
  z-index: 111;
}

.popup {
  position: fixed;
  width: 100%;
  background-color: #002856;
  color: #fff;
  text-align: center;
  overflow: auto;
}

.popup_title {
  display: block;
  margin-top: 50px;
  font-size: 48px;
  font-family: 'RobotoBold', 'Arial', sans-serif;
}

.popup_form {
  margin: 50px auto 0;
  width: 820px;
  display: inline-block;
}

.popup_input {
  width: 90%;
  display: block;
  margin: 6px auto;
  background-color: transparent;
  padding: 10px 5px;
  border: 2px solid #fff;
  color: #fff;
}

.popup_input::placeholder {
  color: #fff;
}

.popup_input:nth-child(1),
.popup_input:nth-child(2) {
  width: 45%;
  display: inline-block;
}

.popup_btn {
  cursor: pointer;
  display: block;
  margin: 10px auto 0;
  font-size: 16px;
  width: 90%;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
  padding: 15px 0;
  background-color: #fff;
  color: #002856;
  transition: .3s all;
}

.close-popup {
  position: absolute;
  top: 70px;
  right: 50px;
  width: 50px;
  height: 50px;
  cursor: pointer;
}

.close-popup:before {
  content: '';
  background-color: #fff;
  position: absolute;
  height: 2px;
  width: 50px;
  top: 11px;
  left: -4px;
  transform: rotate(-45deg);
}

.close-popup:after {
  content: '';
  background-color: #fff;
  position: absolute;
  height: 2px;
  width: 50px;
  top: 11px;
  transform: rotate(45deg);
  left: -4px;
}

.popup .footer_bottom {
  max-width: 750px;
  margin-bottom: 50px;
}

@media screen and (max-width: 1024px) {
  .min {
    display: block;
  }

  .burger {
    display: block;
  }

  .btn_mnu {
    opacity: 1;
    position: absolute;
    top: 10px;
    right: 5px;
  }

  .header {
    margin-top: 10px;
  }

  .header_wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .header_logo {
    position: static;
  }

  .header_menu {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 109;
    width: 100%;
    height: 100vh;
    background-color: #eee;
  }

  .header_menu.active {
    display: block;
  }

  .header_ul {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-top: 20vh;
  }

  .header_li {
    margin: 5px 0;
    padding: 10px 0;
    border-bottom: 1px solid #002856;
  }

  .header_li:last-child {
    border-bottom: none;
  }

  .header_a {
    font-family: 'RobotoRegular', 'Arial', sans-serif;
    letter-spacing: 1px;
  }

  .header_lang {
    display: none;
    z-index: 110;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
  }

  .header_lang.active {
    display: block;
  }

  .header_lang--item {
    background-color: #d9d9d9;
    width: 100%;
    border: none;
    display: flex;
    justify-content: center;
  }

  .header_lang--eng {
    bottom: 45px;
  }

  .header_lang svg {
    display: none;
  }

  .intro {
    background-size: cover;
    border-bottom: 15px solid #002856;
    text-align: center;
  }

  .intro::after {
    display: none;
  }

  .intro_callback {
    float: inherit;
    position: static;
    margin: 0 auto;
    display: inline-block;
    width: 75%;
  }

  .intro_clear {
    display: none;
  }

  .intro_tel {
    font-size: 30px;
  }

  .intro_text {
    float: inherit;
    margin: 50px auto 0;
    position: static;
  }

  .intro_text--h1 {
    font-size: 48px;
  }

  .intro_text--bottom {
    justify-content: center;
  }

  .intro_text--p {
    text-align: left;
  }

  .service_wrap {
    justify-content: center;
  }

  .service_h2 {
    text-align: center;
  }

  .service_item:nth-child(1) {
    margin-left: 22px;
  }

  .service_item:nth-child(3) {
    margin-right: 22px;
  }

  .service_item:nth-child(4) {
    margin-left: 22px;
  }

  .service_item:nth-child(5) {
    margin-right: 22px;
  }

  .slider {
    padding: 65px 0;
  }

  .know_content {
    width: 100%;
    height: auto;
    min-height: 360px;
  }

  .contacts {
    margin-left: 0;
  }

  .intro-contacts .intro_text,
  .intro.intro-sertificates .intro_text {
    width: 80%;
  }
}

@media screen and (max-width: 768px) {
  .popup {
    width: 100%;
  }

  .popup_title {
    margin-top: 80px;
    font-size: 22px;
  }

  .popup_form {
    width: 320px;
    margin-top: 35px;
  }

  .close-popup {
    top: 30px;
    right: 20px;
  }
}

@media screen and (max-width: 600px) {
  .social {
    display: none;
  }

  .header {
    height: 70px;
    margin: 0;
    order: 1;
  }

  .header_wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .header_logo {
    position: absolute;
    top: 70px;
    left: 50%;
    transform: translate(-50%);
  }

  .intro {
    order: 2;
  }

  .intro_callback {
    position: absolute;
    bottom: 0;
    top: auto;
    left: 50%;
    transform: translate(-50%);
  }

  .intro_tel {
    font-size: 22px;
  }

  .intro_text {
    display: none;
  }

  .slider {
    order: 3;
    margin-top: 0;
    padding: 35px 0;
    border-bottom: none;
  }

  .slider_content {
    text-align: center;
    padding: 0;
  }

  .slider_h3 {
    font-size: 22px;
  }

  .slider_p {
    font-size: 16px;
    margin: 20px auto 25px;
  }

  .service {
    margin-top: 0;
    order: 4;
  }

  .service_h2 {
    font-size: 40px;
  }

  .service_h3 {
    font-size: 22px;
    text-align: center;
  }

  .service_item {
    padding: 35px 20px 0;
    justify-content: flex-start;
    height: 320px;
  }

  .service_item br {
    display: none;
  }

  .service_p {
    margin-top: 20px;
  }

  .service_btn {
    text-align: center;
    margin-top: 35px;
  }

  .know {
    margin-top: 50px;
    order: 5;
  }

  .know_content {
    min-height: inherit;
    padding-bottom: 35px;
  }

  .know_h3 {
    margin: 50px auto 20px;
    font-size: 22px;
  }

  .know_p {
    text-align: center;
    margin: 0;
    padding: 0 10px;
  }

  .know_line {
    display: none;
  }

  .know_a {
    bottom: -40px;
    font-size: 14px;
    height: 40px;
    width: 220px;
  }

  .know_a--plus {
    width: 50px;
    line-height: 40px;
  }

  .know_a--text {
    width: 170px;
    line-height: 40px;
  }

  .footer {
    order: 6;
    padding-top: 40px;
  }

  .footer_h3 {
    font-size: 22px;
  }

  .footer_form {
    text-align: center;
    margin: 50px auto 0;
  }

  .footer_form--input {
    font-size: 14px;
    width: 40%;
    padding: 10px 0;
  }

  .footer_form--input:nth-child(2) {
    margin-left: 4%;
  }

  .footer_form--input:nth-child(3) {
    width: 84%;
    margin: 25px auto 0;
  }

  .footer_bottom {
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }

  .footer_bottom--item {
    margin: 10px 0;
  }

  .footer_bottom--item:nth-child(2) {
    display: flex;
  }

  .footer_bottom--item:nth-child(2) div {
    margin-left: 10px;
  }

  .footer_bottom--item:nth-child(3) p {
    display: inline;
  }

  .footer_arrow {
    cursor: pointer;
    margin-top: 50px;
    width: 26px;
    fill: #fff;
    transform: rotate(180deg);
  }

  .contacts {
    margin: 110px 0 0;
    order: 3;
  }

  .intro-contacts .intro_text,
  .intro.intro-sertificates .intro_text {
    width: 100%;
    font-size: 22px;
    height: 115px;
    bottom: -115px;
  }

  .intro-contacts .intro_text--item,
  .intro.intro-sertificates .intro_text--item {
    height: 115px;
    border-top: 15px solid #002856;
  }

  .intro-contacts .intro_text--h1,
  .intro.intro-sertificates .intro_text--h1 {
    font-size: 28px;
  }
}