.header {
    margin-top: 10px;
    &_wrap {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &_logo {
        position: relative;
        top: 35px;
        z-index: 11;
    }
    &_ul {
        display: flex;
    }
    &_li {
        margin: 0 25px;
    }
    &_a {
        .bold();
        color: #002856;
        transition: .3s all;
        &.active {
            color: #d41317;   
        }
        &:hover {
            color: #d41317;
        }
    }
    &_lang {
        position: relative;
        &--item {
            display: flex;
            align-items: center;
            background-color: #eee;
            cursor: pointer;
            height: 45px;
            width: 121px;
            border: 1px solid rgba(232, 232, 232, 1);
            transition: .3s all;
            z-index: 10;
            &:hover {
                background-color: #fff;
            }
        }
        &--left {
            display: flex;
            margin-left: 15px;
        }
        &--eng {
            display: none;
            position: absolute;
            bottom: -45px;
            &.active {
                display: flex;
            }
        }
        svg {
            min-width: 12px;
            width: 12px;
            height: auto;
            margin-left: 20px;
            transition: .3s all;
            position: relative;
            top: -2px;
            &.active {
                transform: rotate(180deg);
            }
        }
        img {
            height: 20px;
        }
        p {
            margin-left: 5px;
            font-size: 14px;
            color: #323232;
        }
    }
}
.min {
    display: none;
}
body.active {
    overflow: hidden;
}
.burger {
    display: none;
}
.btn_mnu {
    width: 50px;
    height: 50px;
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    cursor: pointer;
    position: relative;
    z-index: 110;
    opacity: 0;
    transition: .3s all;
}
.btn_row {
    height: 2px;
    width: 60%;
    margin: 2px 0;
    background-color: @blue;
    position: relative;
    transition: all .5s;
}
.burger.active .btn_row:first-child {
    transform: rotate(-135deg);
    top: 6px;
}
.burger.active .btn_row:last-child {
    transform: rotate(-225deg);
    top: -6px;
}
.burger.active .btn_row:nth-child(2) {
    opacity: 0;
}