.social {
    position: fixed;
    top: 50%; right: 50px;
    transform: translate(0,-50%);
    display: flex;
    flex-direction: column;
    z-index: 101;
    &_fb {
        margin-top: 20px;
    }
}
.main {
    display: flex;
    flex-direction: column;
}
.intro {
    background: url(../img/main.jpg) no-repeat;
    min-height: 534px;
    position: relative;
    &::after {
        content: '';
        position: absolute;
        bottom: 212px; left: 100px;
        height: 15px;
        width: 500px;
        background-color: @blue;
    }
    &_callback {
        float: right;
        margin-right: 60px;
        position: relative;
        top: -20px;
        &--item {
            .bold();
            display: flex;
            flex-direction: column;
            text-align: center;
            color: @blue;
            background-color: #eee;
            padding: 10px 15px;
        }
    }
    &_clear {
        clear: both;
    }
    &_tel {
        font-size: 35px;
    }
    &_time {
        font-size: 14px;
    }
    &_text {
        float: right;
        margin: 240px 60px 0 0;
        position: relative;
        top: -20px;
        height: 400px;
        background-color: #eee;
        z-index: 10;
        &--item {
            padding: 30px 50px 0px;
        }
        &--h1 {
            color: @blue;
            font-size: 58px;
            line-height: 1;
        }
        &--bottom {
            margin-top: 65px;
            display: flex;
            align-items: flex-start;
        }
        &--p {
            max-width: 500px;
            line-height: 1.5;
            letter-spacing: 1px;
            margin-left: 30px;
        }
        &--line {
            margin-top: 5px;
            width: 115px;
            height: 3px;
            background-color: @blue;
        }
        &--a {
            color: #fff;
            background-color: @blue;
            display: flex;
            justify-content: space-around;
            width: 234px;
            height: 52px;
            position: absolute;
            bottom: -52px;
            right: 45px;
            &__plus {
                display: block;
                text-align: center;
                width: 52px;
                font-size: 20px;
                line-height: 52px;
                transition: .3s all;
                &:hover {
                    background-color: #d41317;
                }
            }
            &__text {
                display: block;
                text-align: center;
                width: 182px;
                line-height: 52px;
                transition: .3s all;
                background-color: #d41317;
                &:hover {
                    background-color: @blue;
                }
            }
        }
    }
}
.service {
    margin-top: 120px;
    padding-top: 50px;
    position: relative;
    &::after {
        content: '';
        position: absolute;
        height: 326px;
        width: 100%;
        top: 0; left: 0;
        background-color: @blue; 
        z-index: -1;
    }
    &_content {
        max-width: 960px;
        margin: 0 auto;
        width: 100%;
        padding: 0 15px;
    }
    &_h2 {
        color: #fff;
        font-size: 55px;
    }
    &_wrap {
        margin-top: 35px;
        display: flex;
        flex-wrap: wrap;
        align-items: flex-start;
    }
    &_item {
        width: 279px;
        height: 364px;
        background-color: #eee;
        padding: 35px 0px 20px 25px;
        position: relative;
        margin: 22px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        &:nth-child(1){
            margin-left: 0;
        }
        &:nth-child(3) {
            margin-right: 0;
        }
        &:nth-child(4){
            margin-left: 0;
        }
        &:nth-child(5){
            margin-right: 0;
        }
        &::after {
            content: '';
            border: 3px solid #eee;
            position: absolute;
            width: 100%;
            height: 100%;
            top: 12px;
            left: 7px;
        }
    }
    &_h3 {
        font-size: 28px;
        color: @blue;
    }
    &_p {
        margin-top: 10px;
        max-width: 220px;
        line-height: 1.5;
        letter-spacing: 0.5px;
    }
    &_btn {
        display: inline-block;
        transition: .3s all;
        color: #d41317;
        &:hover {
            color: @blue;
        }
    }
}

.slider {
    margin-top: 65px;
    padding: 65px 0 130px;
    background-color: #eee;
    color: @blue;
    border-bottom: 15px solid @blue;
    &_content {
        text-align: center;
        padding: 0 50px;
    }
    &_h3 {
        font-size: 35px;
    }
    &_p {
        max-width: 750px;
        display: inline-block;
        margin: 45px auto 50px;
        font-size: 31px;
    }
    &_line {
        width: 113px;
        height: 3px;
        background-color: @blue;
        margin: 0 auto;
    }
}
.slick-next {
    right: 0;
}
.slick-prev {
    left: 0;
}
.slick-prev:before, .slick-next:before {
    font-size: 55px;
    color: @blue;
}
.slick-dots li.slick-active {
    background-color: transparent;
    border: 2px solid rgba(0, 40, 86, 1);
    width: 9px;
    height: 9px;
    border-radius: 100%;
}
.slick-dots li{
    background-color: rgba(0, 40, 86, 1);
    border: 2px solid rgba(0, 40, 86, 1);
    width: 7px;
    height: 7px;
    border-radius: 100%;
}
.slick-dots li button:before {
    content: '';
}
.slick-dots {
    position: absolute;
    bottom: -120px;
    left: 50%;
    transform: translate(-50%);
    display: flex;
    justify-content: center;
    align-items: center;
}

.know {
    margin-top: 125px;
    &_content {
        width: 920px;
        height: 360px;
        background-color: #eee;
        position: relative;
        margin: 0 auto;
        text-align: center;
    }
    &_h3 {
        margin: 55px auto 50px;
        display: inline-block;
        font-size: 35px;
        color: @blue;
    }
    &_bottom {
        display: flex;
        align-items: flex-start;
        justify-content: center;
    }
    &_p {
        max-width: 500px;
        line-height: 1.5;
        letter-spacing: 0.5px;
        margin-left: 30px;
        text-align: left;
    }
    &_line {
        margin-top: 5px;
        width: 115px;
        height: 3px;
        background-color: @blue;
    }
    &_a {
        color: #fff;
        background-color: @blue;
        display: flex;
        justify-content: space-around;
        width: 262px;
        height: 52px;
        position: absolute;
        bottom: -52px;
        right: 0px;
        &--plus {
            display: block;
            text-align: center;
            width: 52px;
            font-size: 20px;
            line-height: 52px;
            transition: .3s all;
            &:hover {
                background-color: #d41317;
            }
        }
        &--text {
            display: block;
            text-align: center;
            width: 210px;
            line-height: 52px;
            transition: .3s all;
            background-color: #d41317;
            &:hover {
                background-color: @blue;
            }
        }
    }
}