.intro.intro-contacts {
    background: url(../img/contact.jpg) no-repeat;
    height: 360px;
    min-height: inherit;
    position: relative;
    background-attachment: fixed;
    &::after {
        display: none;
    }
}
.intro.intro-sertificates {
    background: url(../img/certificat.jpg) no-repeat;
    min-height:534px;
    position: relative;
    &::after {
        bottom: -15px;
        left: 0;
    }
}
.intro-contacts .intro_text, 
.intro.intro-sertificates .intro_text {
        float: inherit;
        text-align: center;
        margin: 0 auto;
        height: 250px;
        width: 60%;
        display: inline-block;
        position: absolute;
        bottom: -125px; top: auto;
        left: 50%;
        transform: translate(-50%);
}
.intro-contacts .intro_text--item, 
.intro.intro-sertificates .intro_text--item{
            display: flex;
            justify-content: center;
            align-items: center;
            text-align: center;
            height: 250px;
            padding: 0px 40px;
        }
.intro-contacts .intro_clear, 
.intro.intro-sertificates .intro_clear {
        display: none;
    }
    
.contacts {
        margin: 160px 0 0 250px;
    &-item {
        margin: 20px 0;
        &--h3 {
            font-size: 28px;
            color: @blue;
        }
        &--p {
            margin-top: 15px;
        }
    }
}    


.overlay {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, .8);
    display: none;
    z-index: 111;
}

.popup {
    position: fixed;
    width: 100%;
    background-color: @blue;
    color: #fff;
    text-align: center;
    overflow: auto;
    &_title {
        display: block;
        margin-top: 50px;
        font-size: 48px;
        .bold();
    }
    &_form {
        margin: 50px auto 0;
        width: 820px;
        display: inline-block;
    }
    &_input {
        width: 90%;
        display: block;
        margin: 6px auto;
        background-color: transparent;
        padding: 10px 5px;
        border: 2px solid #fff;
        color: #fff;
        &::placeholder {
            color: #fff;
        }
        &:nth-child(1),
        &:nth-child(2) {
            width: 45%;
            display: inline-block;
        }
        
    }
    &_btn {
        cursor: pointer;
        display: block;
        margin: 10px auto 0;
        font-size: 16px;
        width: 90%;
        font-weight: 600;
        text-transform: uppercase;
        letter-spacing: 1px;
        padding: 15px 0;
        background-color: #fff;
        color: @blue;
        transition: .3s all;
    }
}


.close-popup {
    position: absolute;
    top: 70px;
    right: 50px;
    width: 50px;
    height: 50px;
    cursor: pointer;
}

.close-popup:before {
    content: '';
    background-color: #fff;
    position: absolute;
    height: 2px;
    width: 50px;
    top: 11px;
    left: -4px;
    transform: rotate(-45deg);
}

.close-popup:after {
    content: '';
    background-color: #fff;
    position: absolute;
    height: 2px;
    width: 50px;
    top: 11px;
    transform: rotate(45deg);
    left: -4px;
}
.popup .footer_bottom {
    max-width: 750px;
    margin-bottom: 50px;
}

@media screen and (max-width: 768px) {
    .popup {
        width: 100%;
        &_title {
            margin-top: 80px;
            font-size: 22px;
        }
        &_form {
            width: 320px;
            margin-top: 35px;
        }
    }
    .close-popup {
        top: 30px;
        right: 20px;
    }
}

