.footer {
    margin-top: 120px;
    min-height: 572px;
    background-color: @blue;
    padding-top: 65px;
    color: #fff;
    &_h3 {
        font-size: 35px;
        text-align: center;
    }
    &_form {
        margin: 70px auto 0;
        max-width: 865px;
        &--input {
            border-bottom: 1px solid #fff;
            background-color: transparent;
            padding: 7px;
            width: 47.5%;
            color: #fff;
            &::placeholder {
                color: #fff;
            }
            &:nth-child(2) {
                margin-left: 4%;
            }
            &:nth-child(3) {
                margin-top: 15px;
                display: block;
                border: none;
                border-bottom: 1px solid #fff;
                width: 100%;
            }
        }
        &--submit {
            cursor: pointer;
            display: block;
            background-color: transparent;
            color: #fff;
            text-align: center;
            margin: 20px auto 0;
        }
    }
    &_bottom {
        position: relative;
        margin: 50px auto 0;
        max-width: 865px;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        &--item {
            padding: 0 5px;
            &:nth-child(2) {
                display: flex;
                div {
                    margin-left: 10px;
                }
            }
            &:nth-child(3) {
                p {
                    display: inline;
                }
            }
        }
    }
    &_arrow {
        position: absolute;
        right: 0;
        cursor: pointer;
        margin-top: 50px;
        width: 26px;
        fill: #fff;
        transform: rotate(180deg);
    }
}